* {
  box-sizing: border-box;
  font-size: 16px;
  line-height: 1.3;
}

body {
  color: rgba(0, 0, 0, 0.87);
  font-family: 'Roboto', sans-serif;
  margin: 0;
}

.button {
  appearance: none;
  border: 0;
  border-radius: 3px;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  margin: 0 8px;
  min-height: 36px;
  min-width: 64px;
  padding: 12px 16px;
  position: relative;
}

.button:focus {
  outline: none;
}

.button--raised {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background: #2196f3;
  color: white;
}

.button--raised:active {
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
}

.button--raised:active::before {
  background-color: rgba(0, 0, 0, 0.15);
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.button--raised.button--big {
  font-size: 20px;
  padding: 14px 20px;
}

.button--flat {
  background: transparent;
  color: #2196f3;
  padding: 10px 14px;
}

.button--flat:active::before {
  background-color: rgba(0, 0, 0, 0.15);
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.button--empty {
  background: transparent;
  border: 2px solid #fff;
  color: #fff;
  padding: 10px 14px;
}

.button--empty:active::before {
  background-color: rgba(255, 255, 255, 0.15);
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.button--empty.button--big {
  font-size: 20px;
  padding: 12px 18px;
}

.button--fav {
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
  background: #fff;
  border-radius: 50%;
  color: rgba(0, 0, 0, 0.87);
  height: 60px;
  margin: 0;
  min-height: 0;
  min-width: 0;
  padding: 0;
  width: 60px;
}

.button--fav i {
  font-size: 2.8em;
}

.button--fav:active::before {
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.carousel {
  margin: 0 auto;
  position: relative;
  width: 100%;
}

.carousel__controls {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 40px;
  pointer-events: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  will-change: opacity, pointer-events;
}

.carousel__controls .move-left,
.carousel__controls .move-right {
  pointer-events: all;
}

@media screen and (max-width: 1030px) {
  .carousel__controls {
    display: none;
  }
}

.carousel__items-container {
  display: flex;
  list-style: none;
  margin-left: 50px;
  position: relative;
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  white-space: nowrap;
  width: 95%;
  will-change: transform;
}

.carousel__items-container.is-dragging {
  transition: none;
}

.form {
  display: block;
  margin: 0 auto;
  max-width: 1100px;
  width: 90%;
}

.form fieldset {
  border: 0;
}

.form .actions {
  text-align: right;
}

.form input[type="text"],
.form textarea {
  background-color: #f5f5f5;
  border: 2px solid rgba(0, 0, 0, 0.58);
  border-radius: 2px;
  display: block;
  font-size: 18px;
  margin: 10px 3px;
  padding: 10px;
}

.form textarea {
  font-family: 'Roboto', sans-serif;
  width: 100%;
}

.table {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-collapse: collapse;
  text-align: left;
  width: 100%;
}

.table thead {
  height: auto;
}

.table thead tr {
  height: 56px;
}

.table thead th:first-of-type {
  padding-left: 24px;
}

.table thead th:last-of-type {
  padding-left: 14px;
}

.table thead th {
  color: rgba(0, 0, 0, 0.58);
  font-size: 14px;
  font-weight: 700;
  overflow: hidden;
  padding: 0 12px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.table tbody {
  height: auto;
}

.table tbody tr {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  height: 48px;
}

.table tbody td:first-of-type {
  padding-left: 24px;
}

.table tbody td:last-of-type {
  padding-left: 14px;
}

.table tbody td {
  color: rgba(0, 0, 0, 0.58);
  font-size: 15px;
  padding: 0 12px;
}

.table tfoot {
  height: auto;
}

.table tfoot tr {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  height: 56px;
}

.table tfoot td:first-of-type {
  padding-left: 24px;
}

.table tfoot td:last-of-type {
  padding-left: 14px;
}

.table tfoot td {
  color: rgba(0, 0, 0, 0.58);
  font-size: 15px;
  padding: 0 12px;
}

.list-container {
  margin: 15px;
  width: 100%;
}

.list-container ul {
  margin: 0;
}

.list-title {
  padding: 5px 0;
  width: 100%;
}

.header {
  background-color: transparent;
  display: flex;
  height: 65px;
  justify-content: space-between;
  position: fixed;
  top: 0;
  transition: background 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  z-index: 100;
}

.header--seamed {
  background: #363c40 !important;
}

.header .rwd-menu-icon {
  color: #fff;
  cursor: pointer;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 15px;
  width: 48px;
  will-change: opacity, pointer-events;
}

@media screen and (max-width: 1030px) {
  .header .rwd-menu-icon {
    opacity: 1;
    pointer-events: all;
  }
}

.header .rwd-menu-icon i {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.header__logo {
  position: relative;
}

.header__logo img {
  height: 65px;
  padding: 5px 0 5px 20px;
}

.header__navigation {
  height: 100%;
  margin-right: 20px;
  position: absolute;
  width: 100%;
}

.header nav {
  pointer-events: all;
  position: absolute;
  right: 15px;
  will-change: height;
}

@media screen and (max-width: 1030px) {
  .header nav {
    background-color: #363c40;
    height: 0;
    overflow: hidden;
    position: absolute;
    right: 0;
    text-align: center;
    top: 65px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    width: 100%;
  }
}

.header nav ul {
  display: flex;
  list-style: none;
  padding: 0;
}

@media screen and (max-width: 1030px) {
  .header nav ul {
    display: block;
  }
}

.header nav li {
  line-height: 33px;
  margin: 0 15px;
  white-space: nowrap;
}

@media screen and (max-width: 1030px) {
  .header nav li {
    margin: 16px 0;
  }
}

.header nav a {
  color: #fff;
  font-size: 18px;
  text-decoration: none;
}

.main {
  position: relative;
}

.main.generic {
  margin-top: 100px;
}

.main .title {
  font-size: 1.8em;
  font-weight: bold;
  margin: 5px 0 8px;
  overflow: hidden;
  padding-right: 8px;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

.main .title--small {
  font-size: 1.2em;
}

@media screen and (max-width: 1030px) {
  .main .title {
    font-size: 1.6em;
  }
  .main .title--small {
    font-size: 1.2em;
  }
}

.main .subtitle {
  color: rgba(0, 0, 0, 0.58);
  font-size: 1em;
  font-weight: bold;
  margin: 0;
  overflow: hidden;
  padding-right: 8px;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

.main .subtitle--small {
  font-size: .8em;
}

@media screen and (max-width: 1030px) {
  .main .subtitle {
    font-size: .8em;
  }
  .main .subtitle--small {
    font-size: .8em;
  }
}

.main .resume {
  -webkit-box-orient: vertical;
  display: -webkit-box;
  font-size: 16px;
  margin: 0 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
}

.main .resume.rows-number-6 {
  height: 122.72px;
  -webkit-line-clamp: 6;
}

.main .resume.rows-number-5 {
  height: 101.92px;
  -webkit-line-clamp: 5;
}

.main .resume.rows-number-4 {
  height: 81.12px;
  -webkit-line-clamp: 4;
}

.main .resume.rows-number-3 {
  height: 60.32px;
  -webkit-line-clamp: 3;
}

.main .resume.rows-number-2 {
  height: 39.52px;
  -webkit-line-clamp: 2;
}

.main .section-title {
  margin: 0 auto 16px;
  padding: 0 0 16px;
  text-align: center;
  width: 90%;
}

.main .bordered {
  padding: 15px 0;
}

.main .bordered span {
  color: rgba(0, 0, 0, 0.87);
  font-size: 3.5em;
  font-weight: bold;
  line-height: 1;
}

@media screen and (max-width: 1030px) {
  .main .bordered span {
    font-size: 2.8em;
  }
}

.presentation {
  background-color: #009688;
  display: block;
  position: relative;
}

.presentation__main-message {
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
}

.presentation__main-message h1 {
  color: white;
  font-family: 'Candal', sans-serif;
  font-size: 5em;
  margin: 5px 0;
}

@media screen and (max-width: 1030px) {
  .presentation__main-message h1 {
    font-size: 3em;
  }
}

@media screen and (max-width: 600px) {
  .presentation__main-message h1 {
    font-size: 2.4em;
  }
}

.presentation__main-message h3 {
  color: white;
  font-size: 1.5em;
  margin: 15px 0;
}

@media screen and (max-width: 600px) {
  .presentation__main-message h3 {
    font-size: .8em;
  }
}

.presentation__main-message .actions {
  margin-top: 50px;
  will-change: display;
}

@media screen and (max-width: 600px) {
  .presentation__main-message .actions {
    display: none;
  }
}

.hot-news {
  background-color: #f5f5f5;
  display: block;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 80px 0;
  position: relative;
  width: 100%;
  z-index: 1;
}

.card {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  cursor: pointer;
  display: inline-block;
  height: 350px;
  margin: 20px 10px;
  position: relative;
  vertical-align: top;
  white-space: normal;
  width: 280px;
}

@media screen and (max-width: 1030px) {
  .card {
    width: 230px;
  }
}

.card__image {
  background-color: #ddd;
  height: 30%;
  overflow: hidden;
  position: relative;
}

.card img {
  height: 100%;
  left: 50%;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  will-change: opacity;
}

.card img.loaded {
  opacity: 1;
}

.card__body {
  display: block;
  height: 55%;
  padding: 10px 16px 16px;
  width: 100%;
}

.card__actions {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  bottom: 0;
  max-height: 60px;
  overflow: hidden;
  padding: 4px 0;
  position: absolute;
  width: 100%;
}

.news-and-calendar {
  background-color: #eceff1;
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  justify-content: center;
  min-height: 800px;
  padding: 200px 0;
  position: relative;
  width: 100%;
}

.news-and-calendar::before {
  background: #f5f5f5;
  content: '';
  height: 250px;
  position: absolute;
  top: -200px;
  transform: skewY(3deg);
  width: 100%;
  z-index: 0;
}

.news-and-calendar::after {
  background: #f5f5f5;
  bottom: -200px;
  content: '';
  height: 250px;
  position: absolute;
  transform: skewY(3deg);
  width: 100%;
  z-index: 0;
}

@media screen and (max-width: 1030px) {
  .news-and-calendar {
    flex-wrap: wrap;
  }
}

.news,
.calendar {
  display: flex;
  flex-direction: column;
  width: 50%;
}

@media screen and (max-width: 1030px) {
  .news,
  .calendar {
    width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .news,
  .calendar {
    width: 100%;
  }
}

.news {
  margin: 0 10px 0 20px;
}

@media screen and (max-width: 1030px) {
  .news {
    margin: 0;
  }
}

.calendar {
  align-content: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 20px 0 10px;
  max-width: 666px;
}

@media screen and (max-width: 1030px) {
  .calendar {
    margin: 0;
    max-width: 100%;
  }
}

.article {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  display: flex;
  height: 200px;
  margin: 6px auto;
  max-width: 870px;
  position: relative;
  white-space: normal;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .article {
    margin: 10px 15px;
    width: initial;
  }
}

.article__image {
  background-color: #ddd;
  height: calc(100% + 1px);
  overflow: hidden;
  position: relative;
  width: 260px;
}

.article img {
  height: 100%;
  left: 50%;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: opacity;
}

.article img.loaded {
  opacity: 1;
}

.article__body {
  position: relative;
  width: 100%;
}

.article__body .body-container {
  overflow: hidden;
  padding: 12px 16px;
}

.article__actions {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  bottom: 0;
  max-height: 60px;
  overflow: hidden;
  padding: 4px 0;
  position: absolute;
  text-align: right;
  width: 100%;
}

.article__actions button {
  color: #2196f3;
}

.event {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  height: 200px;
  margin: 6px;
  position: relative;
  white-space: normal;
  width: 200px;
}

@media screen and (max-width: 600px) {
  .event {
    margin: 10px 15px;
    width: 100%;
  }
}

.event--normal {
  background-color: #009688;
}

.event__body {
  color: white;
  display: block;
  padding: 16px;
  width: 100%;
}

.event__body span {
  -webkit-box-orient: vertical;
  display: -webkit-box;
  font-size: 22px;
  height: 114.4px;
  -webkit-line-clamp: 4;
  margin: 0 auto;
  overflow: hidden;
  text-overflow: ellipsis;
}

.event__actions {
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  bottom: 0;
  color: white;
  display: flex;
  justify-content: space-between;
  max-height: 60px;
  overflow: hidden;
  padding: 4px 8px;
  position: absolute;
  width: 100%;
}

.event__actions .date {
  font-size: 12px;
}

.event__actions i {
  line-height: 1.3;
}

.paralax {
  background-color: #f5f5f5;
  height: 100%;
  padding: 0 0 100px;
  position: relative;
  width: 100%;
}

.partners ul {
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding: 0;
  width: 100%;
  will-change: transform;
}

@media screen and (max-width: 1030px) {
  .partners ul {
    flex-wrap: wrap;
  }
}

.partners ul img {
  height: 105px;
  margin: 25px 15px;
}

@media screen and (max-width: 1280px) {
  .partners ul img {
    height: 85px;
  }
}

@media screen and (max-width: 1030px) {
  .partners ul img {
    height: 70px;
  }
}

.main-quote {
  margin: 10% auto 5%;
  width: 80%;
}

@media screen and (max-width: 600px) {
  .main-quote {
    width: 90%;
  }
}

.main-quote .who-wrote {
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
}

.main-quote .sign,
.main-quote .charge {
  font-size: 2em;
  text-align: right;
}

.main-quote .charge {
  font-weight: bold;
}

.main-quote .quote {
  border-left: 9px solid #2196f3;
  font-size: 2.7em;
  font-style: italic;
}

@media screen and (max-width: 1030px) {
  .main-quote .quote {
    font-size: 2em;
  }
}

.contact {
  background-color: #eceff1;
  padding: 80px 0;
}

.contact-input {
  width: 50%;
}

.contact-textarea {
  font-family: 'Roboto', sans-serif;
  width: 100%;
}

.members {
  margin: 50px auto;
  position: relative;
}

.members-container {
  display: flex;
  flex-flow: row wrap;
  margin: 10px auto;
  width: 90%;
}

.member {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  display: inline-block;
  height: 350px;
  margin: 15px;
  width: 250px;
}

.member__image {
  background-color: #ddd;
  height: 45%;
  overflow: hidden;
  position: relative;
}

.member img {
  height: 100%;
  left: 50%;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: opacity;
}

.member img.loaded {
  opacity: 1;
}

.member__body {
  display: block;
  height: 65%;
  padding: 10px 16px 16px;
  width: 100%;
}

.tables {
  background: #eceff1;
  height: auto;
  margin: 50px auto;
  padding: 50px 0;
  position: relative;
}

.tables-container {
  margin: 0 auto;
  max-width: 750px;
  width: 90%;
}

.elementos {
  display: flex;
  flex-direction: row;
  height: auto;
  margin: 50px auto;
  position: relative;
  width: 95%;
}

.elementos__columnas:first-child {
  border: 0;
}

.elementos__columnas:last-child {
  border: 0;
}

.elementos__columnas {
  border-left: 1px solid rgba(0, 0, 0, 0.12);
  border-right: 1px solid rgba(0, 0, 0, 0.12);
  flex: 1 33.333333%;
  margin: 0 20px;
}

.misc {
  display: flex;
  flex-flow: row wrap;
}

.misc__item {
  margin: 20px;
}

.footer {
  background-color: #363c40;
  color: white;
  display: flex;
  flex-flow: row wrap;
  height: 100%;
  justify-content: flex-start;
  padding: 30px 10px 10px;
}

.footer .title {
  color: white;
  font-weight: bold;
}

.footer ul {
  list-style: none;
  padding: 0;
}

.footer__column {
  height: 100%;
  padding: 20px 50px;
}

.footer__column li {
  color: rgba(255, 255, 255, 0.7);
  margin: 5px 0;
}

.footer__column a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
}

.footer__bottom {
  color: rgba(255, 255, 255, 0.7);
  display: flex;
  flex: 1 100%;
  height: 100%;
  justify-content: center;
}

.footer__bottom img {
  height: 20px;
}

.footer .developer {
  padding: 5px 50px;
  position: relative;
  text-align: center;
}

.footer .developer p {
  margin: 0;
}
